





























































import uniNavBar from '../../../components/uni-nav-bar.vue';
var callindex ,roomid;
export default {
	data() {
		return {
			info: {
				
			},
			list: [
			]
		};
	},
	computed:{
		user_name(){
			return function(username){
				return this.$utils.defaultPlusXing(username);
			}
			
		},
		avator() {
			return this.user.avatar!=null && this.user.avatar.length > 0 ? this.user.avatar :
				'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1548997951295&di=0f0d1f98941ac81b5a1210ae2481ca03&imgtype=0&src=http%3A%2F%2Fpic.90sjimg.com%2Fdesign%2F00%2F67%2F59%2F63%2F58e89bee922a2.png'
		},
		user(){
			return this.$store.getters.getUserInfo
		}
	},
	onLoad: function(options) {
		roomid = options.roomid;
		callindex = options.callindex;
		var hbid = options.hbid;
		var data = {
			roomid: roomid,
			hbid: hbid,
		}
		this.$utils.defalsePluginRequest('pl_hb_list', callindex, data,
			res => {
				// suc
				if (res.code == 0) {
					this.info = res.data.info;
					this.list = res.data.list;
				}
			},
			res => {
				// fail
			},
			res => {
				// com
			}
		)
	},
	methods: {
		back: function() {
			uni.navigateBack({
				delta: 1
			});
		},
		showMore: function() {
			uni.navigateTo({
				url: './myRedRecords/myRedRecords?callindex='+callindex+'&roomid='+roomid,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	components: {
		uniNavBar
	}
}
